.contact {
  background-color: rgb(38, 52, 65);
  overflow-x: hidden;
  overflow-y: auto;
}

.title-wrapper {
  position: relative;
  text-align: center;
  color: white;
  overflow: hidden;
}

.contact-body {
  display: grid;
  flex-direction: column;
  align-items: center;
  margin: 80px;
  max-width: 100%;
}

.contact-description {
  display: grid;
  vertical-align: middle;
  line-height: 1.5;
  padding: 20px;
  border: 10px white solid;
  background: rgba(255, 255, 255, 0.5);
  list-style: none;
  margin-bottom: 80px;
}

.contact-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact-content-contex {
  display: grid;
  grid-template-columns: repeat(2, auto);
  list-style: none;
  justify-content: center;
}

.content-type {
  font-family: Josefin-Bold;
  font-style: bold;
  font-size: 23px;
  color: black;
  padding-top: 10px;
}

.content-info {
  font-family: Adventor;
  display: inline;
  font-size: 20px;
  color: rgb(39, 39, 44);
  padding-left: 20px;
  list-style: none;
}

.apply-text {
  font-family: Adventor;
}

.map {
  position: relative;
  width: 50rem;
  height: 50rem;
  border: 10px white solid;
  margin: 50px;
}

@media screen and (max-width: 1300px) {
  .contact-content-contex {
    grid-template-columns: repeat(1, auto);
    margin: 0px;
  }
  .contact-body {
    margin: 0;
    margin-bottom: 0px;
  }
  .contact-description {
    margin: 0px;
    margin-top: 5%;
  }
}

@media screen and (max-width: 900px) {
  .contact-description {
    margin: 0px;
    margin-top: 5%;
    margin-bottom: 5%;
  }
  .contact-content {
    display: grid;
    margin: 0;
  }
  .contact-content-contex {
    display: inline;
  }
  .map {
    margin: 0;
    width: 50vh;
    height: 50vh;
    margin-bottom: 40px;
  }
}
